.App {
  margin:0 auto;
}
a{
  text-decoration:none;
  color:inherit;
}
li{
  list-style-type:none;
}
.container{
  margin:0 50px 0 50px;
}
/* 배너 */
.header{
  display:flex;
  width:100%;
  height:150px;
  align-items:center;
  border-bottom:1px solid #e4e8eb;
}
.header img{
  width:220px;
  height:50px;
  object-fit:cover;
}
.search{
  width:580px;
  height:55px;
  border:2px solid #03cf5d;
  display:flex;
}
.search input{
  width:80%;
  height:35px;
  margin-top:8px;
  font-size:18px;  
  padding-left:10px;
  border:none;
  outline:none;
}
.search img{
  width:30px;
  height:55px;
  object-fit:none;
}
.searchbtn{
  width:8%;
  background-color:#03cf5d;
}

/* 네비 */
.nav{
  border-bottom:1px solid #e4e8eb;
  line-height:40px;
}
.nav ul{
  display:flex;
  margin:0;
}
.nav ul li{
  padding:5px;
  font-size:16px;
  font-weight:700;
  color:#03c75a;
  cursor:pointer;
}
.nav ul li.bknav{
  color:black;
}
.more{
  padding:5px;
  font-size:14px;
  font-weight:400;
  margin:0 0 0 10px;
}
.more img{
  object-fit:cover;
  width:16px;
  vertical-align:middle;
}
.nav ul li.today{
  margin-left:auto;
  margin-right:20px;
  color:black;
  font-size:14px;
}
.nav ul li.today2{
  margin-left:auto;
  margin-right:20px;
  color:black;
  font-size:14px;
}
.nav ul li.today2 img{
  object-fit:cover;
  width:16px;
  vertical-align:middle;
}

.main{
  border-bottom:1px solid #e4e8eb;
}

/* 푸터 */
.footer{
  padding:0 45px 0;
}
.bottom{
  margin-top:20px;
}
.bottom img{
  width:160px;
  height:80px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
